'use client';

import { useParams } from 'next/navigation';
import categories from '../../../data/categories.json';
import products from '../../../data/products.json';
import ProductCard from '@/components/ProductCard';

export default function CategoriaPage() {
  const { slug } = useParams();

  const category = categories.find(cat => cat.slug === slug);
  const categoryProducts = products.filter(prod => prod.category === slug);

  if (!category) {
    return (
      <div className="p-4">
        <h1 className="text-2xl font-bold">Categoria n√£o encontrada</h1>
      </div>
    );
  }

  return (
    <main className="min-h-screen p-4 bg-gray-100">
      {/* Capa da Categoria */}
      <section className="mb-8 text-center">
        <img
          src={category.image}
          alt={category.name}
          className="w-full max-h-64 object-cover rounded-lg mb-4"
        />
        <h1 className="text-4xl font-extrabold text-gray-800 mb-2">{category.name}</h1>
        <p className="text-lg text-gray-600">{category.description}</p>
      </section>

      {/* Produtos da Categoria */}
      <section className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-6">
        {categoryProducts.length > 0 ? (
          categoryProducts.map(product => (
            <ProductCard key={product.id} product={product} />
          ))
        ) : (
          <p className="text-gray-600">Nenhum produto encontrado nesta categoria.</p>
        )}
      </section>
    </main>
  );
}

